import { Component, ViewChild } from '@angular/core';
import { NavController, PopoverController, LoadingController, Loading, ToastController, Tabs, Tab } from 'ionic-angular';

import { StateProvider } from '../../providers/state/state';
import { MapComponent } from '../../components/map/map';
import { HelpPage } from '../help/help';
import { IOperation, IToast } from '../../interfaces/interfaces';

@Component({
    selector: 'page-map',
    templateUrl: 'map.html'
})
export class MapPage {
    //private isTabletOrDesktop = false;
    @ViewChild(MapComponent) private mapComponent: MapComponent;
    private loading: Loading = null;
    public step: Number = 1;
    public operation: IOperation = null;
    public drawExists = false;
    public drawIsValid = false;

    constructor(public navCtrl: NavController,
                private popoverCtrl: PopoverController,
                private stateProvider: StateProvider,
                public loadingCtrl: LoadingController,
                private toastCtrl: ToastController) {
        //this.isTabletOrDesktop = this.platform.is('core') || this.platform.is('tablet');
        // <button ion-fab color="primary" [attr.mini]="!isTabletOrDesktop ? '' : null">
    }

    public ionViewDidLoad() {
        this.loading = this.loadingCtrl.create({ content: 'Loading...' });
        this.loading.present();
    }

    public onMapLoaded() {
        this.loading.dismiss();
        this.loading = null;
    }

    public ionViewWillEnter() {
        this.operation = this.stateProvider.getOperation();
        let previousTab: Tab = (this.navCtrl.parent as Tabs).getSelected();
        if (previousTab.index === 0) { this.step = 1; this.drawExists = false; this.drawIsValid = false; }
    }

    public openHelpPopover(evt) {
        let popover = this.popoverCtrl.create(HelpPage, { title: 'Reshaping ' + this.operation.fieldId, text: 'Step 1. Please draw the line ...Please draw the line ...Please draw the line ...' });
        popover.present({ ev: evt });
    }

    public onDrawExistenceChanged(existence: boolean) {
        this.drawExists = existence;
    }

    public onDrawValidityChanged(validity: boolean) {
        this.drawIsValid = validity;
    }

    public showToast(notification: IToast) {
        let toast = this.toastCtrl.create({
            message: notification.message,
            duration: 5000,
            position: 'middle',
            showCloseButton: true,
            cssClass: notification.type
        });
        toast.present();
    }

    public cancel() {
        this.stateProvider.clearStatus();
        this.navCtrl.parent.select(0);
    }

    public reset() {
        this.mapComponent.resetDraw();
    }

    public previous() {
        if (this.step === 2) {
            this.mapComponent.onStepOneEnter();
            this.step = 1;
        }
    }

    public next() {
        if (this.step === 1) {
            this.mapComponent.onStepTwoEnter();
            this.step = 2;
        } else if (this.step === 2) {
            this.stateProvider.setOperation(this.mapComponent.getOperation());
            this.navCtrl.parent.select(2);
        }
    }


}



