import { Component } from '@angular/core';
import { NavController, Events } from 'ionic-angular';

import { StateProvider } from '../../providers/state/state';
import { MapPage } from '../../pages/map/map';
import { IOperation } from '../../interfaces/interfaces';

@Component({
    selector: 'page-form',
    templateUrl: 'form.html'
})
export class FormPage {
    
    private changeDate: Date;
    private changeReason: string;
    private operation: IOperation = null;
    
    constructor(public navCtrl: NavController, private stateProvider: StateProvider, public events: Events) { }

    public ionViewWillEnter() {
        this.operation = this.stateProvider.getOperation();
    }

    public cancel() {
        this.stateProvider.clearStatus();
        this.navCtrl.parent.select(0);
    }

    public previous() {
        this.navCtrl.parent.select(1);
    }

    public save() {
        this.events.publish('fields:updating', this.changeDate, this.changeReason);
        this.stateProvider.clearStatus();
        this.navCtrl.parent.select(0);
    }

}



