import { Injectable } from '@angular/core';

import * as esriLoader from 'esri-loader';

@Injectable()
export class ArcgisProvider {

  private arcgisApiPromise: Promise<HTMLScriptElement>;

  constructor() {
    this.arcgisApiPromise = esriLoader.loadScript({ url: '//js.arcgis.com/3.23compact/' });
  }

  public loadModules(modules: string[]): Promise<any[]>  {
    return this.arcgisApiPromise.then(() => {
      return esriLoader.loadModules(modules);
    });
  }

}



