import { Injectable } from '@angular/core';

import { IOperation } from '../../interfaces/interfaces';

@Injectable()
export class StateProvider {
    private operation: IOperation = null;

    constructor() { }

    public getOperation(): IOperation {
        return this.operation;
    }

    public setOperation(operation: IOperation): void {
        this.operation = operation;
    }

    public clearStatus() {
        this.operation = null;
    }
}



