import { Component, OnInit, OnChanges, SimpleChanges, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { Events } from 'ionic-angular';

import { ArcgisProvider } from '../../providers/arcgis/arcgis';
import { FieldsProvider } from '../../providers/fields/fields';
import { IOperation, IToast } from '../../interfaces/interfaces';

import { MapOptions, Handle } from "esri";
import Map from "esri/map";
import Graphic from "esri/graphic";
import FeatureLayer from "esri/layers/FeatureLayer";
import FeatureSet from "esri/tasks/FeatureSet";
import Geometry from "esri/geometry/Geometry";
import geometryEngine from "esri/geometry/geometryEngine";
import Point from "esri/geometry/Point";
import Polygon from "esri/geometry/Polygon";
import SimpleFillSymbol from "esri/symbols/SimpleFillSymbol";
import SimpleLineSymbol from "esri/symbols/SimpleLineSymbol";
import SimpleMarkerSymbol from "esri/symbols/SimpleMarkerSymbol";
import Draw from "esri/toolbars/draw";
import Edit from "esri/toolbars/edit";


@Component({
    selector: 'map',
    templateUrl: 'map.html'
})
export class MapComponent implements OnInit {
    @ViewChild('mapDiv') private mapEl: ElementRef;
    @Input() public operation: IOperation;
    @Output() public mapLoaded: EventEmitter<null> = new EventEmitter<null>(true);
    @Output() public drawExistenceChanged: EventEmitter<boolean> = new EventEmitter<boolean>(true);
    @Output() public drawValidityChanged: EventEmitter<boolean> = new EventEmitter<boolean>(true);
    @Output() public eventNotified: EventEmitter<IToast> = new EventEmitter<IToast>(true);

    public map: Map;
    public fieldsFeatureLayer: FeatureLayer;
    public selectionFeatureLayer: FeatureLayer;
    private mapLoadListener: Handle;
    private mapClickListener: Handle;
    private drawFillSymbol: SimpleFillSymbol;
    private drawLineSymbol: SimpleLineSymbol;
    private drawMarkerSymbol: SimpleMarkerSymbol;
    private editVertexSymbol: SimpleMarkerSymbol;
    private editGhostVertexSymbol: SimpleMarkerSymbol;
    private drawToolbar: Draw;
    private editToolbar: Edit;

    constructor(private arcgisProvider: ArcgisProvider, private fieldsProvider: FieldsProvider, public events: Events) {
        this.events.subscribe('fields:updating', (changeDate, changeReason) => {
            this.save();
        });
    }

    public ngOnInit() {

        this.arcgisProvider.loadModules([
            'esri/Color',
            'esri/map',
            'esri/symbols/SimpleFillSymbol',
            'esri/symbols/SimpleLineSymbol',
            'esri/symbols/SimpleMarkerSymbol',
            'esri/toolbars/draw',
            'esri/toolbars/edit'
        ]).then(([
            Color,
            Map,
            SimpleFillSymbol,
            SimpleLineSymbol,
            SimpleMarkerSymbol,
            Draw,
            Edit
        ]) => {

            const mapOptions: MapOptions = {
                center: [-5.9301, 54.5973],
                zoom: 12,
                basemap: "hybrid",
                showAttribution: false,
                showLabels: true
            };

            this.map = new Map(this.mapEl.nativeElement, mapOptions);
            this.mapLoadListener = this.map.on("load", this.mapLoad.bind(this));

            // Draw toolbar fill symbols
            this.drawFillSymbol = new SimpleFillSymbol(
                SimpleFillSymbol.STYLE_SOLID,
                new SimpleLineSymbol(SimpleLineSymbol.STYLE_SOLID, new Color([255, 0, 0, 1]), 2),
                new Color([0, 0, 0, 0.4])
            );
            const drawFillSymbol_dash: SimpleFillSymbol = new SimpleFillSymbol(
                SimpleFillSymbol.STYLE_SOLID,
                new SimpleLineSymbol(SimpleLineSymbol.STYLE_DASH, new Color([255, 0, 0, 1]), 2),
                new Color([0, 0, 0, 0.4])
            );

            // Draw toolbar line symbols
            this.drawLineSymbol = new SimpleLineSymbol(SimpleLineSymbol.STYLE_SOLID, new Color([255, 0, 0, 1]), 2);
            const drawLineSymbol_dash: SimpleLineSymbol = new SimpleLineSymbol(SimpleLineSymbol.STYLE_DASH, new Color([255, 0, 0, 1]), 2);

            // Draw toolbar marker symbol
            this.drawMarkerSymbol = new SimpleMarkerSymbol(
                SimpleMarkerSymbol.STYLE_CIRCLE,
                18,
                new SimpleLineSymbol(SimpleLineSymbol.STYLE_SOLID, new Color([255, 0, 0, 1]), 2),
                new Color([0, 0, 0, 0.4])
            );

            // Draw toolbar
            this.drawToolbar = new Draw(this.map, { showTooltips: false });
            this.drawToolbar.setFillSymbol(drawFillSymbol_dash);
            this.drawToolbar.setLineSymbol(drawLineSymbol_dash);
            this.drawToolbar.on("draw-complete", this.drawComplete.bind(this));

            // Edit toolbar marker symbol
            this.editVertexSymbol = new SimpleMarkerSymbol(
                SimpleMarkerSymbol.STYLE_CIRCLE,
                18,
                new SimpleLineSymbol(SimpleLineSymbol.STYLE_SOLID, new Color([255, 0, 0, 1]), 2),
                new Color([255, 0, 0, 1])
            );
            this.editGhostVertexSymbol = new SimpleMarkerSymbol(
                SimpleMarkerSymbol.STYLE_CIRCLE,
                18,
                new SimpleLineSymbol(SimpleLineSymbol.STYLE_SOLID, new Color([255, 255, 255, 0.25]), 2),
                new Color([0, 0, 0, 0.25])
            );

            // Edit toolbar
            this.editToolbar = new Edit(this.map);
            this.editToolbar.on("vertex-move-stop", this.vertexMoveStop.bind(this));

        });
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (this.map && this.map.loaded && changes.operation) {
            this.init();
        }
    }

    private init() {
        let field: Graphic = this.fieldsFeatureLayer.graphics.find((g: Graphic) => g.attributes["FIELDID"] === this.operation.fieldId);
        this.operation.field = field;
        this.clearGraphics();
        this.setSelection();
        this.zoomToField();
        this.activateDraw();
    }

    private mapLoad() {
        this.mapLoadListener.remove();
        this.arcgisProvider.loadModules(["esri/layers/FeatureLayer"]).then(([FeatureLayer]) => {
            // Add Fields Feature Layer
            const fieldDetails = this.fieldsProvider.getFieldLayerConstructorDetails();
            this.fieldsFeatureLayer = new FeatureLayer(fieldDetails.featureCollection, fieldDetails.options);
            this.map.addLayer(this.fieldsFeatureLayer);
            // Add Selection Feature Layer
            const selectionDetails = this.fieldsProvider.getSelectionLayerConstructorDetails();
            this.selectionFeatureLayer = new FeatureLayer(selectionDetails.featureCollection, selectionDetails.options);
            this.map.addLayer(this.selectionFeatureLayer);
            // Initialize edit operation
            this.init();
            // Notify map page
            this.mapLoaded.emit();
        });

    }

    private mapClick(evt) {
        if (evt.mapPoint) {
            this.arcgisProvider.loadModules(["esri/graphic"]).then(([Graphic]) => {
                this.map.graphics.add(new Graphic(evt.mapPoint, this.drawMarkerSymbol, null, null));
            });
        }
    }

    private zoomToField() {
        let expandFactor: number = 1.1;
        if (this.operation.id === "merge") { expandFactor = 2; }
        this.map.setExtent((<Polygon>this.operation.field.geometry).getExtent().expand(expandFactor), true);
    }

    private clearGraphics() {
        this.map.graphics.clear();
    }

    /******
      DRAW
     ******/

    private activateDraw() {
        this.arcgisProvider.loadModules(["esri/toolbars/draw",]).then(([Draw]) => {
            switch (this.operation.id) {
                case "split":
                case "merge":
                case "reshape":
                    this.drawToolbar.activate(Draw[this.operation.tool]);
                    this.mapClickListener = this.map.on("click", this.mapClick.bind(this));
                    break;
                case "view":
                    if (this.mapClickListener) { this.mapClickListener.remove(); }
                    this.drawToolbar.deactivate();
                    break;
            }
        });
    }

    private deactivateDraw() {
        this.mapClickListener.remove();
        this.drawToolbar.deactivate();
    }

    public resetDraw() {
        this.drawValidityChanged.emit(false);
        this.drawExistenceChanged.emit(false);
        this.clearGraphics();
        this.activateDraw();
    }

    private drawComplete(evt) {
        this.drawExistenceChanged.emit(true);
        this.deactivateDraw();
        this.clearGraphics();
        this.displayDraw(evt.geometry);
        this.validateEdit(evt.geometry);
    }

    private displayDraw(geometry: Geometry) {
        this.arcgisProvider.loadModules(["esri/graphic"]).then(([Graphic]) => {
            let graphic: Graphic = null;
            switch (this.operation.tool) {
                case "POLYLINE":
                    graphic = new Graphic(geometry, this.drawLineSymbol, null, null);
                    this.map.graphics.add(graphic);
                    this.activateEdit(graphic);
                    break;
                case "POINT":
                    graphic = new Graphic(geometry, this.editVertexSymbol, null, null);
                    this.map.graphics.add(graphic);
                    break;
                case "POLYGON":
                    graphic = new Graphic(geometry, this.drawFillSymbol, null, null);
                    this.map.graphics.add(graphic);
                    this.activateEdit(graphic);
                    break;
            }
        });
    }

    private activateEdit(graphic: Graphic) {
        this.arcgisProvider.loadModules(["esri/toolbars/edit"]).then(([Edit]) => {
            let tool = Edit.EDIT_VERTICES.toString();
            var options = {
                allowAddVertices: true,
                allowDeleteVertices: true,
                vertexSymbol: this.editVertexSymbol,
                ghostVertexSymbol: this.editGhostVertexSymbol
            };
            this.map.disableMapNavigation();
            this.editToolbar.activate(tool, graphic, options);
        });

    }

    private deactivateEdit() {
        this.editToolbar.deactivate();
        this.map.enableMapNavigation();
    }

    private vertexMoveStop(evt) {
        this.validateEdit(evt.graphic.geometry);
    }

    private validateEdit(geometry: Geometry) {
        switch (this.operation.id) {
            case "split":
                this.split(geometry);
                break;
            case "merge":
                this.merge(geometry);
                break;
            case "reshape":
                this.reshape(geometry);
                break;
        }
    }

    /***********
      SELECTION
     ***********/

    private setSelection() {
        this.arcgisProvider.loadModules(["esri/graphic", "esri/geometry/geometryEngine"]).then(([Graphic, geometryEngine]) => {
            let selectionSet: Graphic[] = [];
            switch (this.operation.id) {
                case "split":
                case "reshape":
                    selectionSet.push(new Graphic(this.operation.field.toJson()));
                    break;
                case "merge":
                    const intersectingFields = this.fieldsFeatureLayer.graphics
                    .filter((graphic: Graphic) => geometryEngine.intersects(this.operation.field.geometry, graphic.geometry))
                    .filter((graphic: Graphic) => this.operation.field.attributes.OBJECTID !== graphic.attributes.OBJECTID);
                    intersectingFields.forEach((graphic: Graphic) => { selectionSet.push(new Graphic(graphic.toJson())); });
                    //if(intersectingFields.length === 0) { this.toastr.warning("This field has no contiguous fields and cannot be merged.", "Warning."); }
                    break;
            }
            this.operation.selectionSet = selectionSet;
            this.displaySelection();
            // if (this.operation.id === "merge" && selectionSet.length > 0) { this.zoomToSelection(); }
        });
    }

    private displaySelection() {
        // Fields
        let objectIds: number[] = this.operation.selectionSet.map((graphic: Graphic) => graphic.attributes.OBJECTID);
        this.fieldsFeatureLayer.graphics.forEach((graphic: Graphic) => { graphic.visible = objectIds.indexOf(graphic.attributes.OBJECTID) === -1; });
        this.fieldsFeatureLayer.redraw();
        // Selection
        this.selectionFeatureLayer.clear();
        this.operation.selectionSet.forEach((graphic: Graphic) => { this.selectionFeatureLayer.add(graphic); });
        this.selectionFeatureLayer.refresh();
    }

    private clearSelection() {
        // Fields
        this.fieldsFeatureLayer.graphics.forEach((graphic: Graphic) => { graphic.visible = true; });
        this.fieldsFeatureLayer.redraw();
        // Selection
        this.selectionFeatureLayer.clear();
        this.selectionFeatureLayer.refresh();
    }

    private zoomToSelection() {
        this.arcgisProvider.loadModules(["esri/graphic", "esri/graphicsUtils"]).then(([Graphic, graphicsUtils]) => {
            let graphics: Graphic[] = this.operation.selectionSet.slice(0);
            graphics.push(new Graphic(this.operation.field.toJson()));
            const extent = graphicsUtils.graphicsExtent(graphics);
            this.map.setExtent(extent, false);
        });
    }

    /******
      EDIT
     ******/

    private split(geometry: Geometry) {
        this.arcgisProvider.loadModules(["esri/graphic", "esri/geometry/geometryEngine"])
        .then(([Graphic, geometryEngine]) => {
            // Validation (disjoint)
            const intersects: boolean = geometryEngine.intersects(geometry, this.operation.field.geometry);
            if (intersects !== true) {
            this.drawValidityChanged.emit(false);
            this.eventNotified.emit({ message: 'Invalid geometry. The line drawn does not intersect with the target field.', type: 'toast-error' });
            return;
            }
            // Validation (multiple rings). Cut() always returns two geometries.
            let parts = geometryEngine.cut(this.operation.field.geometry, geometry);
            let multiRingParts = parts.filter(function(item){ return item.rings.length > 1 });
            if (multiRingParts.length > 0) {
            this.drawValidityChanged.emit(false);
            this.eventNotified.emit({ message: 'Invalid geometry. The resulting geometry cannot have multiple parts or contain holes.', type: 'toast-error' });
            return;
            }
            /*** PRESET EDIT ***/
            this.operation.geometry = geometry;
            // Get field number(s)
            const fieldSurveyNumber: string = this.operation.field.attributes.FIELDID.substring(0, 9);
            const remainder: string = this.operation.field.attributes.FIELDID.substring(10);
            const fieldNumber: string = remainder.indexOf("/") === -1 ? remainder : remainder.substring(0, remainder.indexOf("/"));
            let newLetterA: string = "A";
            let newLetterB: string = "B";
            if (remainder.indexOf("/") !== -1) {
            const existingLetters = this.fieldsFeatureLayer.graphics
                .map((graphic: Graphic): string => graphic.attributes.FIELDID)
                .filter((fieldId: string) => fieldId.indexOf(fieldSurveyNumber + "/" + fieldNumber) !== -1 )
                .map((fieldId: string) => fieldId.substring(fieldId.lastIndexOf("/") + 1))
                .sort();
            newLetterA = String.fromCharCode(existingLetters[existingLetters.length - 1].charCodeAt(0) + 1);
            newLetterB = String.fromCharCode(existingLetters[existingLetters.length - 1].charCodeAt(0) + 2);
            }
            // Get area(s)
            const area: number = geometryEngine.geodesicArea(this.operation.field.geometry, "hectares");
            const areaA: number = geometryEngine.geodesicArea(parts[0], "hectares");
            const areaB: number = geometryEngine.geodesicArea(parts[1], "hectares");
            const mea: number = this.operation.field.attributes.FNC_MEA_AREA_HA;
            const meaA: number = (areaA/area) * mea;
            const meaB: number = (areaB/area) * mea;
            // Preset adds, updates and deletes
            let addedFeature = new Graphic(this.operation.field.toJson()).setGeometry(parts[0]);
            addedFeature.attributes.FIELDID = fieldSurveyNumber + "/" + fieldNumber + "/" + newLetterA;
            addedFeature.attributes.FNC_CALC_AREA_HA = areaA;
            addedFeature.attributes.FNC_MEA_AREA_HA = meaA;
            let updatedFeature = new Graphic(this.operation.field.toJson()).setGeometry(parts[1]);
            updatedFeature.attributes.FIELDID = fieldSurveyNumber + "/" + fieldNumber + "/" + newLetterB;
            updatedFeature.attributes.FNC_CALC_AREA_HA = areaB;
            updatedFeature.attributes.FNC_MEA_AREA_HA = meaB;
            this.operation.adds = [addedFeature];
            this.operation.updates = [updatedFeature];
            this.drawValidityChanged.emit(true);
        });
    }

    private merge(geometry: Geometry) {
        this.arcgisProvider.loadModules(["esri/graphic", "esri/geometry/geometryEngine"])
        .then(([Graphic, geometryEngine]) => {
            const buffer = geometryEngine.buffer(geometry, 3, "meters");
            // Validation (invalid selection)
            const intersectingFields = this.operation.selectionSet
            .filter((graphic: Graphic) => geometryEngine.intersects(buffer, graphic.geometry));
            if (intersectingFields.length === 0) {
            this.drawValidityChanged.emit(false);
            this.eventNotified.emit({ message: 'Invalid selection. The location clicked does not intersect with any valid field.', type: 'toast-error' });
            return;
            } else if (intersectingFields.length > 1) {
            this.drawValidityChanged.emit(false);
            this.eventNotified.emit({ message: 'Invalid selection. The location clicked intersects with more than one valid field.', type: 'toast-error' });
            return;
            }
            // Validation (multiple rings).
            const union = geometryEngine.union([this.operation.field.geometry, intersectingFields[0].geometry]);
            if (union.rings.length > 1) {
            union.rings.length;
            this.eventNotified.emit({ message: 'Invalid geometry. The resulting geometry cannot have multiple parts or contain holes.', type: 'toast-error' });
            this.drawValidityChanged.emit(false);
            return;
            }
            /*** PRESET EDIT ***/
            this.operation.geometry = geometry;
            // Get field number(s)
            const fieldSurveyNumberA: string = this.operation.field.attributes.FIELDID.substring(0, 9);
            const remainderA: string = this.operation.field.attributes.FIELDID.substring(10);
            const fieldNumberA: number = parseInt(remainderA.indexOf("/") === -1 ? remainderA : remainderA.substring(0, remainderA.indexOf("/")));
            const fieldSurveyNumberB: string = intersectingFields[0].attributes.FIELDID.substring(0, 9);
            const remainderB: string = intersectingFields[0].attributes.FIELDID.substring(10);
            const fieldNumberB: number = parseInt(remainderB.indexOf("/") === -1 ? remainderB : remainderB.substring(0, remainderB.indexOf("/")));
            let newFieldId: string = this.operation.field.attributes.FIELDID;
            if (fieldSurveyNumberA === fieldSurveyNumberB && fieldNumberA > fieldNumberB) {
            newFieldId = intersectingFields[0].attributes.FIELDID;
            }
            // Get area(s)
            const areaA: number = this.operation.field.attributes.FNC_CALC_AREA_HA;
            const meaA: number = this.operation.field.attributes.FNC_MEA_AREA_HA;
            const areaB: number = intersectingFields[0].attributes.FNC_CALC_AREA_HA;
            const meaB: number = intersectingFields[0].attributes.FNC_MEA_AREA_HA;
            const area: number = areaA + areaB;
            const mea: number = meaA + meaB;
            // Preset adds, updates and deletes
            let updatedFeature = new Graphic(this.operation.field.toJson()).setGeometry(union);
            updatedFeature.attributes.FIELDID = newFieldId;
            updatedFeature.attributes.FNC_CALC_AREA_HA = area;
            updatedFeature.attributes.FNC_MEA_AREA_HA = mea;
            let deletedFeature = new Graphic(intersectingFields[0].toJson());
            this.operation.updates = [updatedFeature];
            this.operation.deletes = [deletedFeature];
            this.drawValidityChanged.emit(true);
        });
    }

    private reshape(geometry: Geometry) {
        this.arcgisProvider.loadModules(["esri/graphic", "esri/geometry/geometryEngine"])
        .then(([Graphic, geometryEngine]) => {
            // Validation (disjoint)
            const intersects: boolean = geometryEngine.intersects(geometry, this.operation.field.geometry);
            if (intersects !== true) {
            this.drawValidityChanged.emit(false);
            this.eventNotified.emit({ message: 'Invalid geometry. The area drawn does not intersect with the target field.', type: 'toast-error' });
            return;
            }
            // Validation (multiple rings). Cut() always returns two geometries.
            let diff = geometryEngine.difference(this.operation.field.geometry, geometry);
            if (diff.rings.length > 1) {
            this.drawValidityChanged.emit(false);
            this.eventNotified.emit({ message: 'Invalid geometry. The resulting geometry cannot have multiple parts or contain holes.', type: 'toast-error' });
            return;
            }
            /*** PRESET EDIT ***/
            this.operation.geometry = geometry;
            // Get area(s)
            const prevArea: number = this.operation.field.attributes.FNC_CALC_AREA_HA;
            const prevMea: number = this.operation.field.attributes.FNC_MEA_AREA_HA;
            const area: number = geometryEngine.geodesicArea(diff, "hectares");
            const mea: number = (area/prevArea) * prevMea;
            // Preset adds, updates and deletes
            let updatedFeature = new Graphic(this.operation.field.toJson()).setGeometry(diff);
            updatedFeature.attributes.FNC_CALC_AREA_HA = area;
            updatedFeature.attributes.FNC_MEA_AREA_HA = mea;
            this.operation.updates = [updatedFeature];
            this.drawValidityChanged.emit(true);
        });
    }

    public onStepOneEnter() {
        this.displaySelection();
        this.displayDraw(this.operation.geometry);
    }
  
    public onStepTwoEnter() {
        this.clearGraphics();
        // Fields
        let objectIds = [ this.operation.field.attributes.OBJECTID ];
        if (this.operation.deletes) {
        this.operation.deletes.forEach((graphic: Graphic) => { objectIds.push(graphic.attributes.OBJECTID); });
        }
        // // TODO for 'Add land to this field'
        // if (this.operation.impactedFields) {
        //   this.operation.impactedFields.forEach((graphic: Graphic) => { objectIds.push(graphic.attributes.OBJECTID); });
        // }
        this.fieldsFeatureLayer.graphics.forEach((graphic: Graphic) => { graphic.visible = objectIds.indexOf(graphic.attributes.OBJECTID) === -1; });
        this.fieldsFeatureLayer.redraw();
        // Selection      
        this.selectionFeatureLayer.clear();
        if (this.operation.adds) {
        this.operation.adds.forEach((graphic: Graphic) => { this.selectionFeatureLayer.add(graphic); });
        }
        if (this.operation.updates) {
        this.operation.updates.forEach((graphic: Graphic) => { this.selectionFeatureLayer.add(graphic); });
        }
        this.selectionFeatureLayer.refresh();
    }

    /******
      SAVE
     ******/

    private save() {
        this.fieldsFeatureLayer.applyEdits(this.operation.adds, this.operation.updates, this.operation.deletes, function() {
            this.clearSelection();
            this.events.publish('fields:updated:success', this.fieldsFeatureLayer.graphics);
        }.bind(this), function() {
            this.events.publish('fields:updated:failure');
        });
    }

    /*******
      UTILS
     *******/

    public getOperation(): IOperation {
        return this.operation;
    }

}



