import { Component } from '@angular/core';
import { NavController, NavOptions, Modal, ModalController, ModalOptions, PopoverController, Events,
    LoadingController, Loading, ToastController } from 'ionic-angular';

import { AboutPage } from '../about/about';
import { FiltersPage } from '../filters/filters';
import { ActionsPage } from '../actions/actions';
import { ArcgisProvider } from '../../providers/arcgis/arcgis';
import { FieldsProvider } from '../../providers/fields/fields';
import { StateProvider } from '../../providers/state/state';
import { IOperation, IToast } from '../../interfaces/interfaces';

import Graphic from "esri/graphic";

@Component({
    selector: 'page-home',
    templateUrl: 'home.html'
})
export class HomePage {

    private loading: Loading = null;
    private modalOptions: ModalOptions = {
        enableBackdropDismiss: false
    };
    private navOptions: NavOptions = {
        updateUrl: false,
        isNavRoot: false
    }
    private filterOptions: {
        farmSurveyNumbers: Array<string>,
        fieldNumbers: Array<string>,
        classifications: Array<string>,
        landTypes: Array<string>,
        townlands: Array<string>
    } = {
        farmSurveyNumbers: [],
        fieldNumbers: [],
        classifications: [],
        landTypes: [],
        townlands: []
    };
    private filters: {
        farmSurveyNumber: string,
        fieldNumber: string,
        classification: string,
        landType: string,
        townland: string
    } = {
        farmSurveyNumber: null,
        fieldNumber: null,
        classification: null,
        landType: null,
        townland: null
    };
    public fields: Graphic[];
    public visibleFields: Graphic[] = [];

    constructor(public navCtrl: NavController,
                private modalCtrl: ModalController,
                private popoverCtrl: PopoverController,
                public loadingCtrl: LoadingController,
                private toastCtrl: ToastController,
                private arcgisProvider: ArcgisProvider,
                private fieldsProvider: FieldsProvider,
                private stateProvider: StateProvider,
                public events: Events) {
        this.events.subscribe('fields:updating', (fields: Graphic[]) => {
            this.loading = this.loadingCtrl.create({ content: 'Loading...' });
            this.loading.present();
        });
        this.events.subscribe('fields:updated:success', (fields: Graphic[]) => {
            this.refreshList(fields);
            this.filterList();
            this.loading.dismiss();
            this.showToast({ message: 'Save successful. Changes applied successfully.', type: 'toast-success' });
        });
        this.events.subscribe('fields:updated:failure', (changeDate, changeReason) => {
            this.loading.dismiss();
            this.showToast({ message: 'Save failed. Unable to apply changes.', type: 'toast-error' });
        });
    }

    ionViewDidLoad() {
        this.fieldsProvider.getFields().then((fields: Graphic[]) => {
            this.refreshList(fields);
            this.filterList();
        });
    }

    public refreshList(fields: Graphic[]) {
        // Update field list
        this.fields = fields.sort(this.compareTwoFieldIds);

        // Update filters
        let farms: string[] = [];
        let fieldNumbers: string[] = [];
        let classifications: string[] = [];
        let landTypes: string[] = [];
        let townlands: string[] = [];
        fields.forEach(function(field: Graphic) {
            if (farms.indexOf(field.attributes.FIELDID.substring(0, 9)) === -1) { farms.push(field.attributes.FIELDID.substring(0, 9)); }
            if (fieldNumbers.indexOf(field.attributes.FIELDID.substring(10)) === -1) { fieldNumbers.push(field.attributes.FIELDID.substring(10)); }
            if (classifications.indexOf(field.attributes.FIELD_CLASSIFICATION) === -1) { classifications.push(field.attributes.FIELD_CLASSIFICATION); }
            if (landTypes.indexOf(field.attributes.LANDTYPE) === -1) { landTypes.push(field.attributes.LANDTYPE); }
            if (townlands.indexOf(field.attributes.TOWNLAND) === -1) { townlands.push(field.attributes.TOWNLAND); }
        });
        this.filterOptions.farmSurveyNumbers = farms.sort();
        this.filterOptions.fieldNumbers = fieldNumbers.sort(this.compareTwoFieldIdRemainders);
        this.filterOptions.classifications = classifications.sort();
        this.filterOptions.landTypes = landTypes.sort();
        this.filterOptions.townlands = townlands.sort();
    }

    public filterList() {
        this.visibleFields = this.fields.slice(0);

        if (this.filters.farmSurveyNumber) {
            this.visibleFields = this.visibleFields.filter(f => f.attributes.FIELDID.substring(0, 9) === this.filters.farmSurveyNumber);
        }
        if (this.filters.fieldNumber) {
            this.visibleFields = this.visibleFields.filter(f => f.attributes.FIELDID.substring(10) === this.filters.fieldNumber);
        }
        if (this.filters.classification) {
            this.visibleFields = this.visibleFields.filter(f => f.attributes.FIELD_CLASSIFICATION === this.filters.classification);
        }
        if (this.filters.landType) {
            this.visibleFields = this.visibleFields.filter(f => f.attributes.LANDTYPE === this.filters.landType);
        }
        if (this.filters.townland) {
            this.visibleFields = this.visibleFields.filter(f => f.attributes.TOWNLAND === this.filters.townland);
        }
    }

    public onChangesApplied(fields: Graphic[]) {
        this.refreshList(fields);
        this.filterList();
        //this.modalWindow.closeModal();
    }

    private compareTwoFieldIds(graphic1: Graphic, graphic2: Graphic): number {
        const fsn1: string = graphic1.attributes.FIELDID.toLocaleLowerCase().substring(0, 9);
        const fsn2: string = graphic2.attributes.FIELDID.toLocaleLowerCase().substring(0, 9);
        const remainder1: string = graphic1.attributes.FIELDID.toLocaleLowerCase().substring(10);
        const remainder2: string = graphic2.attributes.FIELDID.toLocaleLowerCase().substring(10);
        const number1: number = parseInt(remainder1.indexOf("/") === -1 ? remainder1 : remainder1.substring(0, remainder1.indexOf("/")));
        const number2: number = parseInt(remainder2.indexOf("/") === -1 ? remainder2 : remainder2.substring(0, remainder2.indexOf("/")));
        if (fsn1 !== fsn2) { return fsn1.localeCompare(fsn2); }
        else if (number1 !== number2) { return number1 - number2; }
        else { return remainder1.localeCompare(remainder2); }
    }

    private compareTwoFieldIdRemainders(remainder1: string, remainder2: string): number {
        const number1: number = parseInt(remainder1.indexOf("/") === -1 ? remainder1 : remainder1.substring(0, remainder1.indexOf("/")));
        const number2: number = parseInt(remainder2.indexOf("/") === -1 ? remainder2 : remainder2.substring(0, remainder2.indexOf("/")));
        if (number1 !== number2) { return number1 - number2; }
        else { return remainder1.localeCompare(remainder2); }
    }

    public openAboutModal() {
        let modal = this.modalCtrl.create(AboutPage, null, this.modalOptions);
        modal.present();
    }

    public openFiltersModal() {
        let modal = this.modalCtrl.create(FiltersPage, { options: this.filterOptions, filters: this.filters }, this.modalOptions);
        modal.present();
        modal.onWillDismiss((data) => {
            this.filters = data.filters;
            this.filterList();
        });
    }

    public openActionsPopover(evt) {
        let popover = this.popoverCtrl.create(ActionsPage, { fieldId: evt.currentTarget.id });
        popover.present({ ev: evt });
        popover.onWillDismiss((data) => {
            if (data != null) {
                const operation: IOperation = {
                    id: data.id,
                    title: data.title,
                    tool: data.tool,
                    fieldId: data.fieldId
                }
                this.stateProvider.setOperation(operation);
                this.navCtrl.parent.select(1);
            }
        });
    }

    public showToast(notification: IToast) {
        let toast = this.toastCtrl.create({
            message: notification.message,
            duration: 3000,
            position: 'middle',
            showCloseButton: true,
            cssClass: notification.type
        });
        toast.present();
    }

}



