import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { HomePage } from '../pages/home/home';
import { MapPage } from '../pages/map/map';
import { FormPage } from '../pages/form/form';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ArcgisProvider } from '../providers/arcgis/arcgis';

import { ComponentsModule } from '../components/components.module';
import { AboutPageModule } from '../pages/about/about.module';
import { ActionsPageModule } from '../pages/actions/actions.module';
import { FiltersPageModule } from '../pages/filters/filters.module';
import { HelpPageModule } from '../pages/help/help.module';
import { FieldsProvider } from '../providers/fields/fields';
import { StateProvider } from '../providers/state/state';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    MapPage,
    FormPage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    ComponentsModule,
    AboutPageModule,
    ActionsPageModule,
    FiltersPageModule,
    HelpPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    MapPage,
    FormPage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ArcgisProvider,
    FieldsProvider,
    StateProvider
  ]
})
export class AppModule {}



