import Graphic from "esri/graphic";
import Geometry from "esri/geometry/Geometry";

export interface IOperation {
    id: string;
    title: string;
    tool: string;
    fieldId: string;
    field?: Graphic;
    geometry?: Geometry;
    selectionSet?: Graphic[];
    impactedFields?: Graphic[];
    adds?: Graphic[];
    updates?: Graphic[];
    deletes?: Graphic[];
};

export interface IToast {
    message: string;
    type: string;
};


